import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step8(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    
    setErrorMsg(!isValid ? "Please enter valid number" : "");
    if (state.USCISNumber.length < 10)
      setErrorMsg("Please enter valid number");
    else 
    setErrorMsg(!isValid ? "Please enter valid number" : "");

  };

   useEffect(() => {
     setTouched(formRef.current.checkValidity());
   }, []);

  return (
    <Step heading={`Do you have USCIS Online Account Number?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "hasUSCISNumber", "Yes");
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "hasUSCISNumber", "No");
                    history.push(nextStep)
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {state.hasUSCISNumber == "Yes" && (
                <>
                  <MDBRow center>
                    <MDBCol>
                      <MDBInput
                        label="USCIS Number"
                        group
                        type="tel"
                        name="USCISNumber"
                        pattern="^[0-9]+$"
                        value={state.USCISNumber}
                        onChange={(e) => {
                          if (e.target.value.length > 11) return;
                          onChangeField(e, e.target.name,e.target.value);
                          // formRef.current.phone.value = normalizeInput(state.phone);
                          validate();
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>

                  <MDBRow
                    center
                    className="d-flex flex-column align-items-center"
                  >
                    {touched && errorMsg.length > 0 && (
                      <div className="text-danger mb-2">{errorMsg}</div>
                    )}
                    {touched && !errorMsg && (
                      <Link to={nextStep}>
                        <MDBBtn
                          color="pink"
                          rounded
                          type="button"
                          className="z-depth-1a"
                        >
                          Next
                        </MDBBtn>
                      </Link>
                    )}
                    {(!touched || errorMsg) && (
                      <MDBBtn
                        color={"grey"}
                        disabled
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    )}
                  </MDBRow>
                </>
              )}
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
