// add conditionals --shubs

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [count, setcount] = useState(1);
  const formRef = new React.createRef();

  const [otherFactors, setOtherFactors] = useState(state.otherFactors1to21_B);

  const Questions = [
    {
      question:
        "Have you EVER been a member of, or in any way associated (either directly or indirectly) with:",
      name: "q10",
      options: [
        {
          opt: "The Communist Party?",
          name: "a",
        },
        {
          opt: "Any other totalitarian party?",
          name: "b",
        },
        {
          opt: "A terrorist organization?",
          name: "c",
        },
      ],
    },
    {
      question:
        "Between March 23, 1933 and May 8, 1945, did you work for or associate in any way (either directly or indirectly) with:",
      name: "q13",
      options: [
        {
          opt: "The Nazi government of Germany?",
          name: "a",
        },
        {
          opt:
            "Any government in any area occupied by, allied with, or established with the help of the Nazi government of Germany?",
          name: "b",
        },
        {
          opt:
            "Any German, Nazi, or S.S. military unit, paramilitary unit, self-defense unit, vigilante unit, citizen unit, police unit, government agency or office, extermination camp, concentration camp, prisoner of war camp, prison, labor camp, or transit camp?",
          name: "c",
        },
      ],
    },
    {
      question: "Were you EVER involved in any way with any of the following:",
      name: "q14",
      options: [
        {
          opt: "Genocide?",
          name: "a",
        },
        {
          opt: "Torture?",
          name: "b",
        },
        {
          opt: " Killing, or trying to kill, someone?",
          name: "c",
        },
        {
          opt: "Badly hurting, or trying to hurt, a person on purpose?",
          name: "d",
        },
        {
          opt:
            "Forcing, or trying to force, someone to have any kind of sexual contact or relations?",
          name: "e",
        },
        {
          opt: " Not letting someone practice his or her religion?",
          name: "f",
        },
      ],
    },
    {
      question:
        "Were you EVER a member of, or did you EVER serve in, help, or otherwise participate in, any of the following groups:",
      name: "q15",
      options: [
        {
          opt: "Military unit?",
          name: "a",
        },
        {
          opt:
            "Paramilitary unit (a group of people who act like a military group but are not part of the official military)?",
          name: "b",
        },
        {
          opt: "Police unit?",
          name: "c",
        },
        {
          opt: "Self-defense unit?",
          name: "d",
        },
        {
          opt:
            "Vigilante unit (a group of people who act like the police, but are not part of the official police)?",
          name: "e",
        },
        {
          opt: "Rebel group?",
          name: "f",
        },
        {
          opt:
            "Guerrilla group (a group of people who use weapons against or otherwise physically attack the military, police, government, or other people)?",
          name: "g",
        },
        {
          opt:
            "Militia (an army of people, not part of the official military)?",
          name: "h",
        },
        {
          opt:
            "Insurgent organization (a group that uses weapons and fights against a government)?",
          name: "i",
        },
      ],
    },
    {
      question:
        "Were you EVER a worker, volunteer, or soldier, or did you otherwise EVER serve in any of the following:",
      name: "q16",
      options: [
        {
          opt: "Prison or jail?",
          name: "a",
        },
        {
          opt: "Prison camp?",
          name: "b",
        },
        {
          opt: "Detention facility (a place where people are forced to stay)?",
          name: "c",
        },
        {
          opt: "Labor camp (a place where people are forced to work)?",
          name: "d",
        },
        {
          opt: "Any other place where people were forced to stay?",
          name: "e",
        },
      ],
    },
  ];

  const validate = () => {
    setTouched(true);

    const isValid = formRef.current.checkValidity();
    setErrorMsg(count < 9 ? "Please answer all the questions" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
    validate();
  }, []);

  const handleChange = (qname, oname, value, qindex) => {
    const values = otherFactors;
    validate();
    setcount(count + 1);
    values[qindex]["opts"][oname] = value;
    setOtherFactors(values);
    onChangeField("e", "otherFactors1to21_B", otherFactors);
  };

  return (
    <Step heading={"Other Factors Continued"}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form
          className="was-validated"
          noValidate
          onSubmit={(e) => e.preventDefault()}
          ref={formRef}
        >
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {Questions.map((q, qindex) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontWeight: "bold",
                          fontSize: "larger",
                        }}
                      >
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>

                  {q.options.map((o, index) => (
                    <MDBRow className="align-items-center mt-3">
                      <MDBCol md={8}>
                        <div>
                          <h6 style={{ width: "100%" }}>
                            {index + 1}. {o.opt}
                          </h6>
                        </div>
                      </MDBCol>
                      <MDBCol>
                        <MDBFormInline className="justify-content-center">
                          <div class="form-check mr-3">
                            <input
                              type="radio"
                              class="form-check-input"
                              name={`${index}`}
                              onClick={() =>
                                handleChange(q.name, o.name, true, qindex)
                              }
                              checked={
                                otherFactors[qindex]["opts"][o.name] === true
                                  ? true
                                  : false
                              }
                              label="Yes"
                              type="radio"
                              id="radio1"
                              style={{ display: "flex" }}
                              containerClass="mr-5"
                              required
                            />
                            <label
                              class="form-check-label"
                              for="materialUnchecked"
                            >
                              Yes
                            </label>
                          </div>
                          <div class="form-check mr-3">
                            <input
                              type="radio"
                              class="form-check-input"
                              name={`${index}`}
                              onClick={() =>
                                handleChange(q.name, o.name, false, qindex)
                              }
                              checked={
                                otherFactors[qindex]["opts"][o.name] === false
                                  ? true
                                  : false
                              }
                              label="Yes"
                              type="radio"
                              id="radio1"
                              style={{ display: "flex" }}
                              containerClass="mr-5"
                              required
                            />
                            <label
                              class="form-check-label"
                              for="materialUnchecked"
                            >
                              No
                            </label>
                          </div>
                        </MDBFormInline>
                      </MDBCol>
                    </MDBRow>
                  ))}
                </MDBCard>
              ))}

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
