import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out required fields" : "");
    };
    
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
    
  return (
    <Step heading={"A little bit about yourself"}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow>
                <MDBCol className="mt-3">
                  <label>Ethnicity</label>
                  <select
                    className="browser-default custom-select"
                    name="part7q1"
                    value={state.part7q1}
                    onChange={(e) => {
                      onChangeField("k", e.target.name, e.target.value);
                    }}
                    required
                  >
                    <option selected disabled>
                      Choose your option
                    </option>

                    <option value={"Hispanic or Latino"}>
                      Hispanic or Latino
                    </option>
                    <option value={"Hispanic or Latino"}>
                      Not Hispanic or Latino
                    </option>
                  </select>
                </MDBCol>
                <MDBCol className="mt-3">
                  <label>Race</label>
                  <select
                    className="browser-default custom-select"
                    name="part7q2"
                    value={state.part7q2}
                    onChange={(e) => {
                      onChangeField("k", e.target.name, e.target.value);
                    }}
                    required
                  >
                    <option selected disabled>
                      Choose your option
                    </option>

                    <option value={"White"}>White</option>
                    <option value={"Asian"}>Asian</option>
                    <option value={"Black or African American"}>
                      Black or African American
                    </option>
                    <option value={"American Indian or Alaska Native"}>
                      American Indian or Alaska Native
                    </option>
                    <option value={"Native Hawaiian or Other Pacific Islander"}>
                      Native Hawaiian or Other Pacific Islander
                    </option>
                  </select>
                </MDBCol>
                <MDBCol className="mt-3">
                  <label>Eye Color</label>
                  <select
                    className="browser-default custom-select"
                    name="part7q5"
                    value={state.part7q5}
                    onChange={(e) => {
                      onChangeField("k", e.target.name, e.target.value);
                    }}
                    required
                  >
                    <option selected disabled>
                      Choose your option
                    </option>

                    <option value={"Black"}>Black</option>
                    <option value={"Blue"}>Blue</option>
                    <option value={"Brown"}>Brown</option>
                    <option value={"Gray"}>Gray</option>
                    <option value={"Green"}>Green</option>
                    <option value={"Hazel"}>Hazel</option>
                    <option value={"Maroon"}>Maroon</option>
                    <option value={"Pink"}>Pink</option>
                    <option value={"Unknown/Other"}>Unknown/Other</option>
                  </select>
                </MDBCol>
              </MDBRow>

              <MDBRow>
                <MDBCol className="mt-3">
                  <label>Hair Color</label>
                  <select
                    className="browser-default custom-select"
                    name="part7q6"
                    value={state.part7q6}
                    onChange={(e) => {
                      onChangeField("k", e.target.name, e.target.value);
                    }}
                    required
                  >
                    <option selected disabled>
                      Choose your option
                    </option>

                    <option value={"Bald"}>Bald</option>
                    <option value={"Black"}>Black</option>
                    <option value={"Bold"}>Bold</option>
                    <option value={"Brown"}>Brown</option>
                    <option value={"Gray"}>Gray</option>
                    <option value={"Red"}>Red</option>
                    <option value={"Sandy"}>Sandy</option>
                    <option value={"White"}>White</option>
                    <option value={"Unknown/Other"}>Unknown/Other</option>
                  </select>
                </MDBCol>

                <MDBCol className="mt-3" md={2}>
                  <label>Height</label>
                  <select
                    className="browser-default custom-select"
                    name="part7q3feet"
                    value={state.part7q3feet}
                    onChange={(e) => {
                      onChangeField("k", e.target.name, e.target.value);
                    }}
                    required
                  >
                    <option selected disabled>
                      Choose your option
                    </option>

                    <option value={"2"}>2 ft</option>
                    <option value={"3"}>3 ft</option>
                    <option value={"4"}>4 ft</option>
                    <option value={"5"}>5 ft</option>
                    <option value={"6"}>6 ft</option>
                    <option value={"7"}>7 ft</option>
                  </select>
                </MDBCol>
                <MDBCol className="mt-3" md={2}>
                  <label className="mt-5"></label>
                  <select
                    className="browser-default custom-select"
                    name="part7q3inches"
                    value={state.part7q3inches}
                    onChange={(e) => {
                      onChangeField("k", e.target.name, e.target.value);
                    }}
                    required
                  >
                    <option selected disabled>
                      Choose your option
                    </option>

                    <option value={"0"}>0 inches</option>
                    <option value={"1"}>1 inches</option>
                    <option value={"2"}>2 inches</option>
                    <option value={"3"}>3 inches</option>
                    <option value={"4"}>4 inches</option>
                    <option value={"5"}>5 inches</option>
                    <option value={"6"}>6 inches</option>
                    <option value={"7"}>7 inches</option>
                    <option value={"8"}>8 inches</option>
                    <option value={"9"}>9 inches</option>
                    <option value={"10"}>10 inches</option>
                    <option value={"11"}>11 inches</option>
                  </select>
                </MDBCol>
                <MDBCol md={4} className="mt-4">
                  <MDBInput
                    label="weight"
                    group
                    type="tel"
                    name="weight"
                    value={state.weight}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched  && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {!touched  && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
