import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step8(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    
    const isValid = formRef.current.checkValidity();
    const v = state.questionA.length + state.questionB.length + state.questionC.length
    if (v > 10)
          setTouched(true);

    setErrorMsg(v>15 ? "Please enter valid SSN" : "");
  };

  useEffect(() => {
    if(state.questionA.length + state.questionB.length + state.questionC.length >= 6)
    setTouched(true)

    
  })

  return (
    <Step heading={`Please answer the below Questions`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center className="mt-5">
            <MDBCol sm={12} md={10}>
              <MDBRow center>
                <MDBCol>
                  <h5>
                    Are You 50 years of age or older and have lived in United
                    States as a lawful permanent resident for periods totaling
                    at least 20 years at the time you file your Form N-400
                  </h5>
                </MDBCol>
                <div>
                  <div className="custom-control custom-checkbox custom-control-inline">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="defaultInline1"
                      name="questionA"
                      checked={state.questionA == "Yes" ? true : false}
                      onChange={(e) => {
                        validate()
                        onChangeField(e, e.target.name, "Yes")
                      }}
                    />
                    <label
                      className="custom-control-label"
                      for="defaultInline1"
                    >
                      Yes
                    </label>
                  </div>

                  <div className="custom-control custom-checkbox custom-control-inline">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="defaultInline2"
                      name="questionA"
                      value={state.questionA}
                      checked={state.questionA == "No" ? true : false}
                      onChange={(e) => {
                        validate()
                        onChangeField(e, e.target.name, "No")
                      }}
                    />
                    <label
                      className="custom-control-label"
                      for="defaultInline2"
                    >
                      No
                    </label>
                  </div>
                </div>
              </MDBRow>
              <MDBRow center className="mt-5">
                <MDBCol>
                  <h5>
                    Are you 55 years of age or older and have lived in the
                    United States as a lawful permanent resident for periods
                    totaling at least 15 years at the time you file your Form
                    N-400?
                  </h5>
                </MDBCol>
                <div>
                  <div className="custom-control custom-checkbox custom-control-inline">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="defaultInline3"
                      name="questionB"
                      checked={state.questionB == "Yes" ? true : false}
                      onChange={(e) => {
                        validate()
                        onChangeField(e, e.target.name, "Yes")
                      }}
                    />
                    <label
                      className="custom-control-label"
                      for="defaultInline3"
                    >
                      Yes
                    </label>
                  </div>

                  <div className="custom-control custom-checkbox custom-control-inline">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="defaultInline4"
                      name="questionB"
                      value={state.questionB}
                      checked={state.questionB == "No" ? true : false}
                      onChange={(e) => {
                        validate()
                        onChangeField(e, e.target.name, "No")
                      }}
                    />
                    <label
                      className="custom-control-label"
                      for="defaultInline4"
                    >
                      No
                    </label>
                  </div>
                </div>
              </MDBRow>
              <MDBRow center className="mt-5">
                <MDBCol>
                  <h5>
                    Are you 55 years of age or older and have lived in the
                    United States as a lawful permanent resident for periods
                    totaling at least 15 years at the time you file your Form
                    N-400?
                  </h5>
                </MDBCol>
                <div>
                  <div className="custom-control custom-checkbox custom-control-inline">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="defaultInline5"
                      name="questionC"
                      checked={state.questionC == "Yes" ? true : false}
                      onChange={(e) => {
                        validate()
                        onChangeField(e, e.target.name, "Yes")
                      }}
                    />
                    <label
                      className="custom-control-label"
                      for="defaultInline5"
                    >
                      Yes
                    </label>
                  </div>

                  <div className="custom-control custom-checkbox custom-control-inline">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="defaultInline6"
                      name="questionC"
                      value={state.questionC}
                      checked={state.questionC == "No" ? true : false}
                      onChange={(e) => {
                        validate()
                        onChangeField(e, e.target.name, "No")
                      }}
                    />
                    <label
                      className="custom-control-label"
                      for="defaultInline6"
                    >
                      No
                    </label>
                  </div>
                </div>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
