//Please list all trips (absences) from the U.S. since becoming a permanent resident?
//Date left/returned to the U.S.? Destination? Gone for over 6 months? Reason for trip?
// 1. destination
// 2. left date
// 3. returned date
// 4. reason for trip

// Employers Name , Employers work address, Current Occupation, dates work at job
//Please list all of your employment history during the last 5 years? Start with your most recent job?

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
} from "mdbreact";
import { countries } from "./countries";
import Step from "./Step";
export default function Step1(props) {
  const blankTrip = {
    destination: "",
    daysOutside: "",
    fromDate: "",
    toDate: "",
    absence: "",
  };
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [trip, setTrip] = useState(state.trip);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out proper values" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  const handleInputChange = (index, name, value) => {
    const values = [...trip];
    if (name === "destination") {
      values[index].destination = value;
      console.log("gotttttt");
    } else if (name === "daysOutside") {
      values[index].daysOutside = value;
    } else if (name === "fromDate") {
      values[index].fromDate = value;
    } else if (name === "toDate") {
      values[index].toDate = value;
    } else if (name === "absence") {
      values[index].absence = value;
    }

    validate();
    setTrip(values);
    onChangeField("e", "trip", trip);
  };

  const addtrip = () => {
    const values = [...trip];
    validate();

    values.push(blankTrip);
    setTrip(values);
  };

  const removetrip = (index) => {
    const values = [...trip];
    values.splice(index, 1);
    setTrip(values);
  };
  const arrToOption = (opt) => ({ value: opt, label: opt });
  return (
    <Step
      heading={
        "Please list all trips (absences) from the U.S. since becoming a permanent resident?"
      }
    >
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={12} md={8}>
              <MDBRow center>
                <h6>If none proceed to next step</h6>
              </MDBRow>
              <MDBRow>
                <MDBCol md={10} className="mt-5 mb-3">
                  <h5>
                    How many total days(24 hours or longer) did you spend
                    outside the united states during the last 5 years?
                  </h5>
                </MDBCol>
                <MDBCol className="mt-3 mb-3">
                  <MDBInput
                    color="pink"
                    label="Days"
                    group
                    type="text"
                    name="totalDaysOutside"
                    value={state.totalDaysOutside}
                    onChange={(event) =>
                      onChangeField(
                        "index",
                        event.target.name,
                        event.target.value
                      )
                    }
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={10} className="mt-5 mb-3">
                  <h5>
                    How many trips of 24 hours or longer did you spend outside
                    the United States during the last 5 years?
                  </h5>
                </MDBCol>
                <MDBCol className="mt-3 mb-3">
                  <MDBInput
                    label="trips"
                    group
                    type="number"
                    name="totalTrips"
                    value={state.totalTrips}
                    onChange={(event) =>
                      onChangeField(
                        "e",
                        event.target.name,
                        event.target.value
                      )
                    }
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol>
                  <h3 className="text-center">
                    List all the trips of 24 hours or longer
                  </h3>
                </MDBCol>
              </MDBRow>
              {trip.map((t, index) => (
                <MDBCard style={{ marginTop: "10px" }} key={`${t}-${index}`}>
                  <div style={{ padding: "10px" }}>
                    <MDBRow center>
                      <MDBCol className="mt-3">
                        <label>Destination</label>
                        <select
                          className="browser-default custom-select"
                          name="destination"
                          value={t.destination}
                          onChange={(e) => {
                            handleInputChange(
                              index,
                              e.target.name,
                              e.target.value
                            );
                          }}
                          required
                        >
                          <option selected disabled>
                            Choose your option
                          </option>
                          {countries.map((country) => (
                            <option value={country.name}>{country.name}</option>
                          ))}
                        </select>
                      </MDBCol>
                    </MDBRow>
                    <MDBRow center></MDBRow>
                    <MDBRow center className="mt-4">
                      <MDBCol>
                        <MDBInput
                          label="Left US on "
                          group
                          type="date"
                          name="fromDate"
                          value={t.fromDate}
                          onChange={(event) =>
                            handleInputChange(
                              index,
                              event.target.name,
                              event.target.value
                            )
                          }
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                      <MDBCol>
                        <MDBInput
                          label=" Returned to US on"
                          group
                          type="date"
                          name="toDate"
                          value={t.toDate}
                          onChange={(event) =>
                            handleInputChange(
                              index,
                              event.target.name,
                              event.target.value
                            )
                          }
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>
                    <MDBRow center className="mt-4">
                      <MDBCol>
                        <label>Absence over 6 months</label>
                        <select
                          className="browser-default custom-select"
                          name="absence"
                          value={t.absence}
                          onChange={(e) => {
                            handleInputChange(
                              index,
                              e.target.name,
                              e.target.value
                            );
                          }}
                          required
                        >
                          <option selected disabled>
                            Choose your option
                          </option>
                          <option value="yes">yes</option>
                          <option value="no">no</option>
                        </select>
                      </MDBCol>
                      <MDBCol md={8}>
                        <MDBInput
                          color="pink"
                          label="No of days outside US"
                          group
                          type="text"
                          name="daysOutside"
                          value={t.daysOutside}
                          onChange={(event) =>
                            handleInputChange(
                              index,
                              event.target.name,
                              event.target.value
                            )
                          }
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>
                  </div>
                </MDBCard>
              ))}
              <MDBRow className="d-flex  justify-items-center mt-5">
                <MDBCol className="d-flex justify-content-center">
                  {touched && !errorMsg && (
                    <MDBBtn label="+" rounded onClick={() => addtrip()}>
                      +
                    </MDBBtn>
                  )}
                  {(!touched || errorMsg) && (
                    <MDBBtn
                      label="+"
                      rounded
                      color={"grey"}
                      disabled
                      onClick={() => addtrip()}
                    >
                      +
                    </MDBBtn>
                  )}
                </MDBCol>
                <MDBCol className="d-flex justify-content-center">
                  <MDBBtn label="+" onClick={() => removetrip()}>
                    -
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
