import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {countries} from "./countries"

export default function Step8(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please enter all required fields" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={`Is one of your parent a US Citizen ?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "parentUSCitizen", "Yes");
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "parentUSCitizen", "No");
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={10}>
              {state.parentUSCitizen == "Yes" && (
                <>
                  <MDBRow center>
                    <MDBCol sm={10} md={10}>
                      <MDBRow center className="mt-5">
                        <h3>
                          Where your parents married after your 18th Birthday?
                        </h3>
                        <MDBCol md={6} lg={4}>
                          <MDBBtn
                            color="pink"
                            rounded
                            type="button"
                            className="btn-block z-depth-1a"
                            onClick={(e) => {
                              onChangeField(
                                e,
                                "marriedBefore18Birthday",
                                "Yes"
                              );
                            }}
                          >
                            Yes
                          </MDBBtn>
                        </MDBCol>
                        <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                        <MDBCol md={6} lg={4}>
                          <MDBBtn
                            color="pink"
                            rounded
                            type="button"
                            className="btn-block z-depth-1a"
                            onClick={(e) => {
                              onChangeField(e, "marriedBefore18Birthday", "No");
                            }}
                          >
                            No
                          </MDBBtn>
                        </MDBCol>
                      </MDBRow>
                    </MDBCol>
                  </MDBRow>
                  {state.marriedBefore18Birthday && (
                    <>
                      <MDBCard className="mt-5 p-4">
                        <MDBRow center>
                          <MDBCol sm={10} md={10}>
                            <h3 className="text-center mb-3">
                              <strong>I. Information About Mother</strong>
                            </h3>
                            <h4 className="text-center">
                              IS your mother a US Citizen ?
                            </h4>
                            <MDBRow center>
                              <MDBCol md={6} lg={4}>
                                <MDBBtn
                                  color="pink"
                                  rounded
                                  type="button"
                                  className="btn-block z-depth-1a"
                                  onClick={(e) => {
                                    onChangeField(e, "motherUScitizen", "Yes");
                                  }}
                                >
                                  Yes
                                </MDBBtn>
                              </MDBCol>
                              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                              <MDBCol md={6} lg={4}>
                                <MDBBtn
                                  color="pink"
                                  rounded
                                  type="button"
                                  className="btn-block z-depth-1a"
                                  onClick={(e) => {
                                    onChangeField(e, "motherUScitizen", "No");
                                  }}
                                >
                                  No
                                </MDBBtn>
                              </MDBCol>
                            </MDBRow>
                            {state.motherUScitizen == "No" && (
                              <h5
                                className="text-center mt-4"
                                style={{ color: "red" }}
                              >
                                Please continue to Father Information
                              </h5>
                            )}
                            {state.motherUScitizen == "Yes" && (
                              <>
                                <MDBRow>
                                  <MDBCol md={12} className="mt-5">
                                    <h5>
                                      Current Legal name of U.S Citizen Mother
                                    </h5>
                                  </MDBCol>
                                  <MDBCol md={4}>
                                    <MDBInput
                                      color="pink"
                                      label="First Name"
                                      group
                                      type="text"
                                      name="motherfirstName"
                                      value={state.motherfirstName}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-0"
                                    />
                                  </MDBCol>
                                  <MDBCol md={4}>
                                    <MDBInput
                                      color="pink"
                                      label="Middle Name"
                                      group
                                      type="text"
                                      name="mothermiddleName"
                                      value={state.mothermiddleName}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      containerClass="mb-0 mt-0"
                                    />
                                  </MDBCol>
                                  <MDBCol md={4}>
                                    <MDBInput
                                      label="Last Name"
                                      group
                                      type="text"
                                      name="motherlastName"
                                      value={state.motherlastName}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-0"
                                    />
                                  </MDBCol>
                                  <MDBCol sm={10} md={6}>
                                    <label className="mt-3 mb-0">
                                      Country of Birth
                                    </label>
                                    <select
                                      className="browser-default custom-select"
                                      name="motherCountryOfBirth"
                                      value={state.motherCountryOfBirth}
                                      onChange={(e) => {
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                    >
                                      <option selected disabled>
                                        Choose your option
                                      </option>
                                      {countries.map((country) => (
                                        <option value={country.name}>
                                          {country.name}
                                        </option>
                                      ))}
                                    </select>
                                  </MDBCol>
                                  <MDBCol md={4} className="mt-5">
                                    <MDBInput
                                      label="Date of Birth"
                                      group
                                      type="date"
                                      name="motherDOB"
                                      value={state.motherDOB}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-0"
                                    />
                                  </MDBCol>
                                  <MDBCol className="mt-4" sm={10} md={6}>
                                    <MDBInput
                                      label="Date mother became a US citizen"
                                      group
                                      type="date"
                                      name="motherUSCitizenDate"
                                      value={state.motherUSCitizenDate}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-3"
                                    />
                                  </MDBCol>
                                  <MDBCol md={4} className="mt-4">
                                    <MDBInput
                                      label="Alien Number"
                                      group
                                      type="tel"
                                      name="motherAlienNumber"
                                      value={state.motherAlienNumber}
                                      onChange={(e) => {
                                        if (state.motherAlienNumber.length > 9)
                                          return
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-3"
                                    />
                                  </MDBCol>
                                </MDBRow>
                              </>
                            )}
                          </MDBCol>
                        </MDBRow>
                      </MDBCard>
                      <MDBCard className="mt-5 p-4">
                        <MDBRow center>
                          <MDBCol sm={10} md={10}>
                            <h3 className="text-center mb-3">
                              <strong>Information About Father</strong>
                            </h3>
                            <h4 className="text-center">
                              IS your Father a US Citizen ?
                            </h4>
                            <MDBRow center>
                              <MDBCol md={6} lg={4}>
                                <MDBBtn
                                  color="pink"
                                  rounded
                                  type="button"
                                  className="btn-block z-depth-1a"
                                  onClick={(e) => {
                                    onChangeField(e, "fatherUScitizen", "Yes");
                                  }}
                                >
                                  Yes
                                </MDBBtn>
                              </MDBCol>
                              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                              <MDBCol md={6} lg={4}>
                                <MDBBtn
                                  color="pink"
                                  rounded
                                  type="button"
                                  className="btn-block z-depth-1a"
                                  onClick={(e) => {
                                    onChangeField(e, "fatherUScitizen", "No");
                                    history.push(nextStep);
                                  }}
                                >
                                  No
                                </MDBBtn>
                              </MDBCol>
                            </MDBRow>
                            {state.fatherUScitizen == "No" && (
                              <h5 className="text-center mt-4" style={{color:"red"}}>Please Continue to Next Step</h5>
                            )}
                            {state.fatherUScitizen == "Yes" && (
                              <>
                                <MDBRow>
                                  <MDBCol md={12} className="mt-5">
                                    <h5>
                                      Current Legal name of U.S Citizen Father
                                    </h5>
                                  </MDBCol>
                                  <MDBCol md={4}>
                                    <MDBInput
                                      color="pink"
                                      label="First Name"
                                      group
                                      type="text"
                                      name="fatherfirstName"
                                      value={state.fatherfirstName}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-0"
                                    />
                                  </MDBCol>
                                  <MDBCol md={4}>
                                    <MDBInput
                                      color="pink"
                                      label="Middle Name"
                                      group
                                      type="text"
                                      name="fathermiddleName"
                                      value={state.fathermiddleName}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      containerClass="mb-0 mt-0"
                                    />
                                  </MDBCol>
                                  <MDBCol md={4}>
                                    <MDBInput
                                      label="Last Name"
                                      group
                                      type="text"
                                      name="fatherlastName"
                                      value={state.fatherlastName}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-0"
                                    />
                                  </MDBCol>
                                  <MDBCol sm={10} md={6}>
                                    <label className="mt-3 mb-0">
                                      Country of Birth
                                    </label>
                                    <select
                                      className="browser-default custom-select"
                                      name="fatherCountryOfBirth"
                                      value={state.fatherCountryOfBirth}
                                      onChange={(e) => {
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                    >
                                      <option selected disabled>
                                        Choose your option
                                      </option>
                                      {countries.map((country) => (
                                        <option value={country.name}>
                                          {country.name}
                                        </option>
                                      ))}
                                    </select>
                                  </MDBCol>
                                  <MDBCol md={4} className="mt-5">
                                    <MDBInput
                                      label="Date of Birth"
                                      group
                                      type="date"
                                      name="fatherDOB"
                                      value={state.fatherDOB}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-0"
                                    />
                                  </MDBCol>
                                  <MDBCol className="mt-4" sm={10} md={6}>
                                    <MDBInput
                                      label="Date father became a US citizen"
                                      group
                                      type="date"
                                      name="fatherUSCitizenDate"
                                      value={state.fatherUSCitizenDate}
                                      onChange={(e) => {
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-3"
                                    />
                                  </MDBCol>
                                  <MDBCol md={4} className="mt-4">
                                    <MDBInput
                                      label="Alien Number"
                                      group
                                      type="tel"
                                      name="fatherAlienNumber"
                                      value={state.fatherAlienNumber}
                                      onChange={(e) => {
                                         if (state.motherAlienNumber.length > 9)
                                           return;
                                        validate();
                                        onChangeField(
                                          e,
                                          e.target.name,
                                          e.target.value
                                        );
                                      }}
                                      required
                                      containerClass="mb-0 mt-3"
                                    />
                                  </MDBCol>
                                </MDBRow>
                              </>
                            )}
                          </MDBCol>
                        </MDBRow>
                      </MDBCard>
                    </>
                  )}
                </>
              )}

              {state.fatherUScitizen == "Yes" &&(<MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>)}
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
const normalizeInput = (value, previousValue) => {
  if (!value) return value;
  const currentValue = value.replace(/[^\d]/g, "");
  // xxx-xx-xxxx
  // 012-34-5678
  if (!previousValue || value.length > previousValue.length) {
    if (currentValue.length <= 3) return currentValue;
    if (currentValue.length === 3) return `${currentValue}-`;
    if (currentValue.length <= 5)
      return `${currentValue.slice(0, 3)}-${currentValue.slice(3)}`;
    if (currentValue.length === 5)
      return `${currentValue.slice(0, 3)}-${currentValue.slice(3)}-`;
    return `${currentValue.slice(0, 3)}-${currentValue.slice(
      3,
      5
    )}-${currentValue.slice(5)}`;
  }
};
