import React, {useEffect, useState} from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step4(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
    
  return (
    <Step heading={`What is your Marital Status?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "maritalStatus", "Single, Never Married");
                    history.push(nextStep);
                  }}
                >
                  Single, Never Married
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    history.push(nextStep);

                    onChangeField(e, "maritalStatus", "Married");
                  }}
                >
                  Married
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    history.push(nextStep);

                    onChangeField(e, "maritalStatus", "Widowed");
                  }}
                >
                  Widowed
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    history.push(nextStep);

                    onChangeField(e, "maritalStatus", "Divorced");
                  }}
                >
                  Divorced
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    history.push(nextStep);

                    onChangeField(e, "maritalStatus", "Separated");
                  }}
                >
                  Separated
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    history.push(nextStep);
                    onChangeField(e, "maritalStatus", "Marriage Anulled");
                  }}
                >
                  Marriage Anulled
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
