import React, { useEffect, useState } from "react";
import { Link, Redirect } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
export default function Step4(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out required fields" : "");
  };
 
  if (state.maritalStatus === "Single, Never Married") {
    return <Redirect to="/start/21" />;
  }

  if (
    state.maritalStatus === "Widowed" ||
    state.maritalStatus === "Divorced" ||
    state.maritalStatus === "Marriage Anulled"
  ) {
    return <Redirect to="/start/19" />;
  }

  return (
    <Step heading={`Please provide the following Details about your current Spouse`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />

            <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
              <h5 className=" mt-5">Current Spouse's Legal Name</h5>
              <MDBRow>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="First Name"
                    group
                    type="text"
                    name="currentSpousefirstName"
                    value={state.currentSpousefirstName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="Middle Name"
                    group
                    type="text"
                    name="currentSpousemiddleName"
                    value={state.currentSpousemiddleName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    label="Last Name"
                    group
                    type="text"
                    name="currentSpouselastName"
                    value={state.currentSpouselastName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <h5 className="mt-5">Current Spouse's Previous Legal Name</h5>
              <MDBRow>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="First Name*"
                    group
                    type="text"
                    name="currentSpousepreviousfirstName"
                    value={state.currentSpousepreviousfirstName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="Middle Name"
                    group
                    type="text"
                    name="currentSpousepreviousmiddleName"
                    value={state.currentSpousepreviousmiddleName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    label="Last Name*"
                    group
                    type="text"
                    name="currentSpousepreviouslastName"
                    value={state.currentSpousepreviouslastName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <h5 className="mt-5">Other Names Used by Current Spouse </h5>
              <h6>(include nicknames, aliases and maiden name)</h6>
              <MDBRow>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="First Name*"
                    group
                    type="text"
                    name="currentSpouseOtherfirstName"
                    value={state.currentSpouseOtherfirstName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="Middle Name"
                    group
                    type="text"
                    name="currentSpouseOthermiddleName"
                    value={state.currentSpouseOthermiddleName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    label="Last Name*"
                    group
                    type="text"
                    name="currentSpouseOtherlastName"
                    value={state.currentSpouseOtherlastName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow className="mt-5">
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="Spouse Date of Birth*"
                    group
                    type="date"
                    name="spouseDOB"
                    value={state.spouseDOB}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={4}>
                  <MDBInput
                    color="pink"
                    label="Married On"
                    group
                    type="Date"
                    name="DateOfMarriage"
                    value={state.DateOfMarriage}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </form>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
