import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    
    const isValid =
      formRef.current.checkValidity() && state.alienNumber.length < 8;
      setErrorMsg(!isValid ? "Please fill out required fields" : "");
      setErrorMsg(state.alienNumber.length < 8 ?"Please enter a valid alien number" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={"	You are at least 18 years of age and:"}>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={10}>
              <MDBRow center>
                <MDBCol>
                  <div class="form-check mr-3 mt-3">
                    <input
                      type="radio"
                      class="form-check-input"
                      name="Part1EligibilityA"
                                          onClick={() => {
                          validate();
                        onChangeField("other", "Part1Eligibility", "A");
                        onChangeField("other", "Part1EligibilityA", "1");
                        setTouched(true);
                      }}
                      checked={state.Part1Eligibility === "A" ? true : false}
                      label="Yes"
                      type="radio"
                      id="radio1"
                      style={{ display: "flex" }}
                      containerClass="mr-5"
                      required
                    />
                    <label class="form-check-label" for="materialUnchecked">
                      A. Have been a lawful permanent resident of the United
                      States for at least 5 years.
                    </label>
                  </div>
                  <div class="form-check mr-2 mt-3">
                    <input
                      type="radio"
                      class="form-check-input mr-1"
                      name="Part1EligibilityA"
                                          onClick={() => {
                          validate();
                        onChangeField("other", "Part1Eligibility", "B");
                        onChangeField("other", "Part1EligibilityB", "1");
                       
                      }}
                      checked={state.Part1Eligibility === "B" ? true : false}
                      label="No"
                      type="radio"
                      id="radio2"
                      style={{ display: "flex" }}
                    />
                    <label class="form-check-label" for="materialUnchecked">
                      B. Have been a lawful permanent resident of the United
                      States for at least 3 years. In addition, you have been
                      married to and living with the same U.S. citizen spouse
                      for the last 3 years, and your spouse has been a U.S.
                      citizen for the last 3 years at the time you filed your
                      Form N-400.
                    </label>
                  </div>
                  <div class="form-check mr-2 mt-3">
                    <input
                      type="radio"
                      class="form-check-input mr-1"
                      name="Part1EligibilityA"
                                          onClick={() => {
                          validate();
                        onChangeField("other", "Part1Eligibility", "C");
                        onChangeField("other", "Part1EligibilityC", "1");
                        
                      }}
                      checked={state.Part1Eligibility === "C" ? true : false}
                      label="No"
                      type="radio"
                      id="radio2"
                      style={{ display: "flex" }}
                    />
                    <label class="form-check-label" for="materialUnchecked">
                      C. Are a lawful permanent resident of the United States
                      and you are the spouse of a U.S. citizen and your U.S.
                      citizen spouse is regularly engaged in specified
                      employment abroad. (See the Immigration and Nationality
                      Act (INA) section 319(b).) If your residential address is
                      outside the United States and you are filing under Section
                      319(b), select the USCIS Field Office from the list below
                      where you would like to have your naturalization
                      interview:
                    </label>
                  </div>
                  {state.Part1EligibilityC === "1" && (
                    <MDBInput
                      color="pink"
                      label="region"
                      group
                      type="text"
                      name="Part1Eligibility"
                      value={state.Part1EligibilityotherC}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      containerClass="mb-0"
                    />
                  )}

                  <div class="form-check mr-2 mt-3">
                    <input
                      type="radio"
                      class="form-check-input mr-1"
                      name="Part1EligibilityA"
                                          onClick={() => {
                          validate();
                        onChangeField("other", "Part1Eligibility", "D");
                        onChangeField("other", "Part1EligibilityD", "1");
                       
                      }}
                      checked={state.Part1Eligibility === "D" ? true : false}
                      label="No"
                      type="radio"
                      id="radio2"
                      style={{ display: "flex" }}
                    />
                    <label class="form-check-label" for="materialUnchecked">
                      D. Are applying on the basis of qualifying military
                      service.
                    </label>
                  </div>
                  <div class="form-check mr-2 mt-3">
                    <input
                      type="radio"
                      class="form-check-input mr-1"
                      name="Part1EligibilityA"
                                          onClick={() => {
                          validate();
                        onChangeField("other", "Part1EligibilityE", "1");
                        onChangeField("other", "Part1Eligibility", "E");
                        
                      }}
                      checked={state.Part1Eligibility === "E" ? true : false}
                      label="No"
                      type="radio"
                      id="radio2"
                      style={{ display: "flex" }}
                    />
                    <label class="form-check-label" for="materialUnchecked">
                      E. Other (Explain):
                    </label>
                  </div>
                  {state.Part1EligibilityE === "1" && (
                    <MDBInput
                      color="pink"
                      label="other"
                      group
                      type="text"
                      name="Part1Eligibility"
                      value={state.Part1EligibilityotherE}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      containerClass="mb-0 mt-2"
                    />
                  )}
                </MDBCol>
              </MDBRow>
              <h3 className="text-center mt-3">Your Alien Number</h3>
              <MDBRow center>
                <MDBInput
                  color="pink"
                  label="Alien Number"
                  group
                  type="tel"
                  name="alienNumber"
                  value={state.alienNumber}
                                  onChange={(e) => {
                                      setTouched(true);
                      if(state.alienNumber.length>9) return 
                    validate();
                    onChangeField(e, e.target.name, e.target.value);
                  }}
                  required
                  containerClass="mb-0"
                />
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
