import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";
import { countries } from "./countries";
import BackBtn from "../../assets/back-btn.png";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out required fields" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={" Is your current spouse a US citizen?"}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <h6 className="text-center"></h6>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow center>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "spouseUScitizen", "Yes");
                    }}
                  >
                    Yes
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "spouseUScitizen", "No");
                    }}
                  >
                    No
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
              {state.spouseUScitizen == "Yes" && (
                <>
                  <h3 className="text-center mt-5">
                    When Did your Current Spouse Become a US citizen
                  </h3>
                  <MDBRow center>
                    <MDBCol md={6} lg={4}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="btn-block z-depth-1a"
                        onClick={(e) => {
                          onChangeField(e, "spouseUScitizenWhen", "At Birth");
                          history.push(nextStep);
                        }}
                      >
                        At Birth
                      </MDBBtn>
                    </MDBCol>
                    <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                    <MDBCol md={6} lg={4}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="btn-block z-depth-1a"
                        onClick={(e) => {
                          onChangeField(e, "spouseUScitizenWhen", "Other");
                        }}
                      >
                        Other
                      </MDBBtn>
                    </MDBCol>
                  </MDBRow>

                  {state.spouseUScitizenWhen == "Other" && (
                    <MDBRow className="mt-4">
                      <MDBCol md={10}>
                        <MDBInput
                          color="pink"
                          label="Spouse Became US Citizen on"
                          group
                          type="Date"
                          name="spouseUSCitizenDate"
                          value={state.spouseUSCitizenDate}
                          onChange={(e) => {
                            validate();
                            onChangeField(e, e.target.name, e.target.value);
                          }}
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>
                  )}
                </>
              )}
              <>
                {state.spouseUScitizen == "No" && (
                  <MDBRow className="mt-4">
                    <MDBCol className="mt-3">
                      <label>Spouse country of Citizenship</label>
                      <select
                        className="browser-default custom-select"
                        name="spouseCountryOfCitizenship"
                        value={state.spouseCountryOfCitizenship}
                        onChange={(e) => {
                          onChangeField("k", e.target.name, e.target.value);
                        }}
                        required
                      >
                        <option selected disabled>
                          Choose your option
                        </option>
                        {countries.map((country) => (
                          <option value={country.name}>{country.name}</option>
                        ))}
                      </select>
                    </MDBCol>
                    <MDBCol md={4} className="mt-4">
                      <MDBInput
                        label="Alien Number"
                        group
                        type="tel"
                        name="fatherAlienNumber"
                        value={state.fatherAlienNumber}
                        onChange={(e) => {
                          if (state.motherAlienNumber.length > 9) return;
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0 mt-3"
                      />
                    </MDBCol>
                    <MDBCol md={12}>
                      <h3 className="text-center mt-5" md={12}>
                        Spouse Current Immigration Status
                      </h3>
                    </MDBCol>
                    <MDBCol md={0} lg={2}></MDBCol>
                    <MDBCol md={6} lg={4}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="btn-block z-depth-1a"
                        onClick={(e) => {
                          onChangeField(
                            e,
                            "spouseImmigrationStatus",
                            "Lawful Permanent Resident"
                          );
                          history.push(nextStep);
                        }}
                      >
                        Lawful Permanent Resident
                      </MDBBtn>
                    </MDBCol>
                    <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                    <MDBCol md={6} lg={4}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="btn-block z-depth-1a"
                        onClick={(e) => {
                          onChangeField(e, "spouseImmigrationStatus", "Other");
                        }}
                      >
                        Other
                      </MDBBtn>
                    </MDBCol>
                  </MDBRow>
                )}
                <>
                  {state.spouseImmigrationStatus == "Other" && (
                    <>
                      <MDBRow>
                        <MDBCol md={10}>
                          <MDBInput
                            color="pink"
                            label="Other Immigration Status"
                            group
                            type="text"
                            name="otherImmigrationStatus"
                            value={state.otherImmigrationStatus}
                            onChange={(e) => {
                              validate();
                              onChangeField(e, e.target.name, e.target.value);
                            }}
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                    </>
                  )}
                </>
              </>
              {state.spouseUScitizenWhen && (
                <>
                  <MDBRow
                    center
                    className="d-flex flex-column align-items-center"
                  >
                    <Link to={nextStep}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    </Link>
                  </MDBRow>
                </>
              )}
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
