import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(0);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [count, setcount] = useState(0);

  const [otherFactors, setOtherFactors] = useState(state.otherFactors22to28);

  const Questions = [
    {
      question:
        "Have you EVER committed, assisted in committing, or attempted to commit, a crime or offense for which you were NOT arrested?",
      name: "q22",
    },
    {
      question:
        "Have you EVER been arrested, cited, or detained by any law enforcement officer (including any immigration official or any official of the U.S. armed forces) for any reason?",
      name: "q23",
    },
    {
      question:
        "Have you EVER been arrested, cited, or detained by any law enforcement officer (including any immigration official or any official of the U.S. armed forces) for any reason?",
      name: "q24",
    },
    {
      question: "Have you EVER been convicted of a crime or offense?",
      name: "q25",
    },
    {
      question:
        "Have you EVER been placed in an alternative sentencing or a rehabilitative program (for example, diversion, deferred prosecution, withheld adjudication, deferred adjudication)?",
      name: "q26",
    },
  ];

  const validate = () => {
    setTouched(true);
    var isValid = false;
    if (formRef.current !== null) {
      isValid = formRef.current.checkValidity();
    }
    setErrorMsg(count < 4 ? "Please fill out proper values" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
    validate();
  }, []);

  const handleChange = (name, value) => {
    const values = otherFactors;
    values[name] = value;
    setOtherFactors(values);
    setcount(count + 1);
    validate();
    onChangeField("e", "otherFactors22to28", otherFactors);
  };

  
  const ChangeField = (ind, name, value) => {
    const values = state.otherFactors22to28_multiple;
    values[name] = value;
    onChangeField("othe", "otherFactors22to28_multiple", values);
  };


  return (
    <Step heading={" Other Factors "}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <h6>
        If any of following apply to you, you must answer "Yes" even if your
        records have been sealed, expunged, or otherwise cleared. You must
        disclose this information even if someone, including a judge, law
        enforcement officer, or attorney, told you that it no longer constitutes
        a record or told you that you do not have to disclose the information.
      </h6>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {Questions.map((q, index) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                        {". "}
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, true);
                            setTouched(true);
                          }}
                          checked={otherFactors[q.name] === true ? true : false}
                          label="Yes"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          required
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, false);
                            setTouched(true);
                          }}
                          checked={
                            otherFactors[q.name] === false ? true : false
                          }
                          label="No"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                </MDBCard>
              ))}

              <MDBCol md={12}>
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        Have you EVER received a suspended sentence, been placed
                        on probation, or been paroled?
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="q18"
                          onClick={() => {
                            ChangeField("other", "q18", "Yes");
                            setTouched(true);
                          }}
                          checked={
                            state.otherFactors1to21_multiple["q18"] === "Yes"
                              ? true
                              : false
                          }
                          label="Yes"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          required
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name="q18"
                          onClick={() => {
                            ChangeField("other", "q18", "No");
                            setTouched(true);
                          }}
                          checked={
                            state.otherFactors1to21_multiple["q18"] === "No"
                              ? true
                              : false
                          }
                          label="No"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                  {state.otherFactors1to21_multiple["q18"] == "Yes" && (
                    <>
                      <MDBCol md={12}>
                        <div>
                          <h6
                            style={{
                              width: "100%",
                              fontSize: "larger",
                              fontWeight: "bold",
                            }}
                          >
                            If you answered "Yes," have you completed the
                            probation or parole?
                          </h6>
                        </div>
                      </MDBCol>
                      <MDBCol>
                        <MDBFormInline className="justify-content-center">
                          <div class="form-check mr-3">
                            <input
                              type="radio"
                              class="form-check-input"
                              name={`q18a`}
                              onClick={() => {
                                ChangeField("other", "q18a", "Yes");
                                setTouched(true);
                              }}
                              checked={
                                state.otherFactors1to21_multiple["q18a"] ===
                                "Yes"
                                  ? true
                                  : false
                              }
                              label="Yes"
                              type="radio"
                              id="radio1"
                              style={{ display: "flex" }}
                              containerClass="mr-5"
                              required
                            />
                            <label
                              class="form-check-label"
                              for="materialUnchecked"
                            >
                              Yes
                            </label>
                          </div>
                          <div class="form-check mr-2">
                            <input
                              type="radio"
                              class="form-check-input mr-1"
                              name={"q18a"}
                              onClick={() => {
                                ChangeField("other", "q18a", "No");
                                setTouched(true);
                              }}
                              checked={
                                state.otherFactors1to21_multiple["q18a"] ===
                                "No"
                                  ? true
                                  : false
                              }
                              label="No"
                              type="radio"
                              id="radio2"
                              style={{ display: "flex" }}
                            />
                            <label
                              class="form-check-label"
                              for="materialUnchecked"
                            >
                              No
                            </label>
                          </div>
                        </MDBFormInline>
                      </MDBCol>
                    </>
                  )}
                </MDBCard>
              </MDBCol>

              <MDBCol md={12}>
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        Have you EVER been in jail or prison?
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="q18"
                          onClick={() => {
                            ChangeField("other", "q18", "Yes");
                            setTouched(true);
                          }}
                          checked={
                            state.otherFactors1to21_multiple["q18"] === "Yes"
                              ? true
                              : false
                          }
                          label="Yes"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          required
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name="q18"
                          onClick={() => {
                            ChangeField("other", "q18", "No");
                            setTouched(true);
                          }}
                          checked={
                            state.otherFactors1to21_multiple["q18"] === "No"
                              ? true
                              : false
                          }
                          label="No"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                  {state.otherFactors1to21_multiple["q18"] == "Yes" && (
                    <>
                      <MDBCol md={12}>
                        <div>
                          <h6
                            style={{
                              width: "100%",
                              fontSize: "larger",
                              fontWeight: "bold",
                            }}
                          >
                            how long were you in jail or prison?
                          </h6>
                        </div>
                      </MDBCol>
                      <MDBCol>
                        <MDBFormInline className="justify-content-center">
                          <div class="form-check ">
                            <MDBInput
                              type="tel"
                              label="Years"
                              name="q28byears"
                              color="pink"
                              value={state.q28byears}
                              onChange={(e) =>
                                onChangeField(
                                  "other",
                                  "q28byears",
                                  e.target.value
                                )
                              }
                            />
                          </div>
                          <div class="form-check ">
                            <MDBInput
                              type="tel"
                              label="Months"
                              name="q28bmonths"
                              color="pink"
                              value={state.q28bmonths}
                              onChange={(e) =>
                                onChangeField(
                                  "other",
                                  "q28bmonths",
                                  e.target.value
                                )
                              }
                            />
                          </div>
                          <div class="form-check">
                            <MDBInput
                              type="tel"
                              label="Days"
                              name="q28bdays"
                              color="pink"
                              value={state.q28bdays}
                              onChange={(e) =>
                                onChangeField(
                                  "other",
                                  "q28bdays",
                                  e.target.value
                                )
                              }
                            />
                          </div>
                        </MDBFormInline>
                      </MDBCol>
                    </>
                  )}
                </MDBCard>
              </MDBCol>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
