import React from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step5(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;

  return (
    <Step
      heading={`Are you requesting an accomodation because of your disabilities and/or impairments?`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "part3Disabilities", "Yes");
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "part3Disabilities", "No");
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
        {state.part3Disabilities == "Yes" && (
          <>
            <MDBRow>
              <div className="custom-control custom-checkbox custom-control-inline mt-5">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  id="defaultInline1"
                  name="deaf"
                  checked={state.deaf == "Yes" ? true : false}
                  onChange={(e) => {
                    onChangeField(e, e.target.name, "Yes");
                  }}
                />
                <label className="custom-control-label" for="defaultInline1">
                  I am deaf or hard of hearing and request the following
                  accomodation.
                </label>
              </div>
              <MDBCol md={12}>
                <MDBInput
                  color="pink"
                  label="Request for accomodation"
                  outline
                  group
                  type="text"
                  name="deafRequest"
                  value={state.deafRequest}
                  onChange={(e) => {
                    onChangeField(e, e.target.name, e.target.value);
                  }}
                  required
                  containerClass="mb-0 mt-0"
                />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <div className="custom-control custom-checkbox custom-control-inline mt-5">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  id="defaultInline2"
                  name="blind"
                  checked={state.blind == "Yes" ? true : false}
                  onChange={(e) => {
                    onChangeField(e, e.target.name, "Yes");
                  }}
                />
                <label className="custom-control-label" for="defaultInline2">
                  I am blind or have low vision and request the following
                  accomodation
                </label>
              </div>
              <MDBCol md={12}>
                <MDBInput
                  color="pink"
                  label="Request for accomodation"
                  outline
                  group
                  type="text"
                  name="blindRequest"
                  value={state.blindRequest}
                  onChange={(e) => {
                    onChangeField(e, e.target.name, e.target.value);
                  }}
                  required
                  containerClass="mb-0 mt-0"
                />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <div className="custom-control custom-checkbox custom-control-inline mt-5">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  id="defaultInline3"
                  name="otherDisability"
                  checked={state.otherDisability == "Yes" ? true : false}
                  onChange={(e) => {
                    onChangeField(e, e.target.name, "Yes");
                  }}
                />
                <label className="custom-control-label" for="defaultInline3">
                  I have another type of disability and/or impairment (for
                  example, use a wheelchair). (Descirbe the nature of your
                  disability and/or impairment and the accomodation you are
                  requesting)
                </label>
              </div>
              <MDBCol md={12}>
                <MDBInput
                  color="pink"
                  label="Request for accomodation"
                  outline
                  group
                  type="text"
                  name="otherDisabilityRequest"
                  value={state.otherDisabilityRequest}
                  onChange={(e) => {
                    onChangeField(e, e.target.name, e.target.value);
                  }}
                  required
                  containerClass="mb-0 mt-0"
                />
              </MDBCol>
            </MDBRow>
              <MDBRow center>
                <MDBCol>
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                </MDBCol>
              </MDBRow>
            
          </>
        )}
      </MDBContainer>
    </Step>
  );
}
