//Gender, Date of birth, date you became a permanent citizen, country of birth, country of citizenship
//dob, gender

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";
import { countries } from "./countries";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out required fields" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={"What is your Gender?"}>
      <h6 className="text-center"></h6>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow center>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "gender", "Male");
                    }}
                  >
                    Male
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "gender", "Female");
                    }}
                  >
                    Female
                  </MDBBtn>
                </MDBCol>
              </MDBRow>

              {state.gender && (
                <>
                  <MDBRow className="mt-5">
                    <MDBCol md={6}>
                      <MDBInput
                        color="pink"
                        label="Date Of Birth"
                        group
                        type="date"
                        name="dob"
                        value={state.dob}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol md={6}>
                      <MDBInput
                        label="became a Permanent Resident on"
                        group
                        type="date"
                        name="permanentResidentDate"
                        value={state.permanentResidentDate}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow>
                    <MDBCol className="mt-3">
                      <label>Spouse country of Citizenship</label>
                      <select
                        className="browser-default custom-select"
                        name="PriorSpouseCountryOfCitizenship"
                        value={state.PriorSpouseCountryOfCitizenship}
                        onChange={(e) => {
                          onChangeField("k", e.target.name, e.target.value);
                        }}
                        required
                      >
                        <option selected disabled>
                          Choose your option
                        </option>
                        {countries.map((country) => (
                          <option value={country.name}>{country.name}</option>
                        ))}
                      </select>
                    </MDBCol>
                    <MDBCol className="mt-3">
                      <label>Spouse country of Citizenship</label>
                      <select
                        className="browser-default custom-select"
                        name="PriorSpouseCountryOfBirth"
                        value={state.PriorSpouseCountryOfBirth}
                        onChange={(e) => {
                          onChangeField("k", e.target.name, e.target.value);
                        }}
                        required
                      >
                        <option selected disabled>
                          Choose your option
                        </option>
                        {countries.map((country) => (
                          <option value={country.name}>{country.name}</option>
                        ))}
                      </select>
                    </MDBCol>
                  </MDBRow>

                  <MDBRow
                    center
                    className="d-flex flex-column align-items-center"
                  >
                    {touched && errorMsg.length > 0 && (
                      <div className="text-danger mb-2">{errorMsg}</div>
                    )}
                    {touched && !errorMsg && (
                      <Link to={nextStep}>
                        <MDBBtn
                          color="pink"
                          rounded
                          type="button"
                          className="z-depth-1a"
                        >
                          Next
                        </MDBBtn>
                      </Link>
                    )}
                    {(!touched || errorMsg) && (
                      <MDBBtn
                        color={"grey"}
                        disabled
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    )}
                  </MDBRow>
                </>
              )}
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
