import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";


export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out required fields" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={"Have you used any other names since Birth?"}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <h6 className="text-center">
        (include nicknames and maiden names if applicable)
      </h6>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow center className="mt-3">
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "othernamesfrombirth", "Yes");
                    }}
                  >
                    Yes
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "othernamesfrombirth", "No");
                      history.push(nextStep);
                    }}
                  >
                    No
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
              {state.othernamesfrombirth == "Yes" && (
                <MDBRow className="mt-5">
                  <MDBCol md={4}>
                    <MDBInput
                      color="pink"
                      label="First Name"
                      group
                      type="text"
                      name="additionalFirstName"
                      value={state.additionalFirstName}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      containerClass="mb-0"
                    />
                  </MDBCol>
                  <MDBCol md={4}>
                    <MDBInput
                      color="pink"
                      label="Middle Name"
                      group
                      type="text"
                      name="additionalMiddleName"
                      value={state.additionalMiddleName}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      containerClass="mb-0"
                    />
                  </MDBCol>
                  <MDBCol md={4}>
                    <MDBInput
                      label="Last Name"
                      group
                      type="text"
                      name="additionalLastName"
                      value={state.additionalLastName}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      containerClass="mb-0"
                    />
                  </MDBCol>
                </MDBRow>
              )}

              {state.othernamesfrombirth == "Yes" && (
                <>
                  <h3 className="text-center mt-4">
                    Is you name different on Permanent Resident Card ?
                  </h3>
                  <MDBRow center className="mt-3">
                    <MDBCol md={6} lg={4}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="btn-block z-depth-1a"
                        onClick={(e) => {
                          onChangeField(e, "namedifferentonpermanent", "Yes");
                        }}
                      >
                        Yes
                      </MDBBtn>
                    </MDBCol>
                    <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                    <MDBCol md={6} lg={4}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="btn-block z-depth-1a"
                        onClick={(e) => {
                          onChangeField(e, "namedifferentonpermanent", "No");
                          history.push(nextStep);
                        }}
                      >
                        No
                      </MDBBtn>
                    </MDBCol>
                  </MDBRow>
                </>
              )}
              <>
                {state.namedifferentonpermanent == "Yes" && (
                  <>
                    <h3 className="text-center mt-5">
                      Your Name Exactly as it appears on Permanent Resisdent
                      Card
                    </h3>
                    <MDBRow>
                      <MDBCol md={4}>
                        <MDBInput
                          color="pink"
                          label="First Name"
                          group
                          type="text"
                          name="permanentResidentCardfirstName"
                          value={state.permanentResidentCardfirstName}
                          onChange={(e) => {
                            validate();
                            onChangeField(e, e.target.name, e.target.value);
                          }}
                          containerClass="mb-0"
                        />
                      </MDBCol>
                      <MDBCol md={4}>
                        <MDBInput
                          color="pink"
                          label="Middle Name"
                          group
                          type="text"
                          name="permanentResidentCardmiddleName"
                          value={state.permanentResidentCardmiddleName}
                          onChange={(e) => {
                            validate();
                            onChangeField(e, e.target.name, e.target.value);
                          }}
                          containerClass="mb-0"
                        />
                      </MDBCol>
                      <MDBCol md={4}>
                        <MDBInput
                          label="Last Name"
                          group
                          type="text"
                          name="permanentResidentCardlastName"
                          value={state.permanentResidentCardlastName}
                          onChange={(e) => {
                            validate();
                            onChangeField(e, e.target.name, e.target.value);
                          }}
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>

                    <MDBRow
                      center
                      className="d-flex flex-column align-items-center"
                    >
                      {touched && errorMsg.length > 0 && (
                        <div className="text-danger mb-2">{errorMsg}</div>
                      )}
                      {touched && !errorMsg && (
                        <Link to={nextStep}>
                          <MDBBtn
                            color="pink"
                            rounded
                            type="button"
                            className="z-depth-1a"
                          >
                            Next
                          </MDBBtn>
                        </Link>
                      )}
                      {(!touched || errorMsg) && (
                        <MDBBtn
                          color={"grey"}
                          disabled
                          rounded
                          type="button"
                          className="z-depth-1a"
                        >
                          Next
                        </MDBBtn>
                      )}
                    </MDBRow>
                  </>
                )}
              </>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
