import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";
import { countries } from "./countries";
import BackBtn from "../../assets/back-btn.png";


export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out required fields" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={"Information About your Current Spouse"}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow>
                <h5 className="mt-5">
                  How many times has your current Spouse been married
                </h5>
                <MDBCol md={4} className="mt-3">
                  <MDBInput
                    color="pink"
                    label="No of times married"
                    group
                    type="text"
                    name="noOfTimesCurrentSpouseMarried"
                    value={state.noOfTimesCurrentSpouseMarried}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0 "
                  />
                </MDBCol>
              </MDBRow>
              {state.noOfTimesCurrentSpouseMarried > 0 && (
                <>
                  <h3 className="text-center mt-5" md={12}>
                    Provide the details of Current Spouse's Prior Spouse
                  </h3>
                  <MDBRow>
                    <MDBCol md={4}>
                      <MDBInput
                        color="pink"
                        label="First Name"
                        group
                        type="text"
                        name="currentSpousePriorSpousefirstName"
                        value={state.currentSpousePriorSpousefirstName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol md={4}>
                      <MDBInput
                        color="pink"
                        label="Middle Name"
                        group
                        type="text"
                        name="currentSpousePriorSpousemiddleName"
                        value={state.currentSpousePriorSpousemiddleName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol md={4}>
                      <MDBInput
                        label="Last Name"
                        group
                        type="text"
                        name="currentSpousePriorSpouselastName"
                        value={state.currentSpousePriorSpouselastName}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>

                  <MDBRow center className="mt-4">
                    <MDBCol md={4} className="align-text-start">
                      <h5 className="text-center">Immigration Status</h5>
                    </MDBCol>
                    <MDBCol>
                      <MDBRow>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="phonetype"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "immigrationStatusOfSpousePriorSpouse",
                                "U.S Citizen"
                              );
                            }}
                            checked={
                              state.immigrationStatusOfSpousePriorSpouse ==
                              "U.S Citizen"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            U.S Citizen{" "}
                          </label>
                        </div>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="phonetype"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "immigrationStatusOfSpousePriorSpouse",
                                "Lawful Permanent Resident"
                              );
                            }}
                            checked={
                              state.immigrationStatusOfSpousePriorSpouse ===
                              "Lawful Permanent Resident"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Lawful Permanent Resident{" "}
                          </label>
                        </div>

                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="phonetype"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "immigrationStatusOfSpousePriorSpouse",
                                "other"
                              );
                            }}
                            checked={
                              state.immigrationStatusOfSpousePriorSpouse ===
                              "other"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            other{" "}
                          </label>
                        </div>
                      </MDBRow>
                    </MDBCol>
                  </MDBRow>
                  <MDBRow>
                    {" "}
                    {state.immigrationStatusOfSpousePriorSpouse == "other" && (
                      <MDBCol>
                        <MDBInput
                          color="pink"
                          label="Explain Other"
                          group
                          type="text"
                          name="immigrationStatusCurrentSpousePriorSpouseOther"
                          value={
                            state.immigrationStatusCurrentSpousePriorSpouseOther
                          }
                          onChange={(e) => {
                            validate();
                            onChangeField(e, e.target.name, e.target.value);
                          }}
                          containerClass="mb-0 mt-0"
                        />
                      </MDBCol>
                    )}
                  </MDBRow>
                  <MDBRow className="mt-3">
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Date of Birth"
                        group
                        type="Date"
                        name="dobCurrentSpousePriorSpouse"
                        value={state.dobCurrentSpousePriorSpouse}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0 mt-5"
                      />
                    </MDBCol>
                    <MDBCol className="mt-3">
                      <label>Spouse country of Citizenship</label>
                      <select
                        className="browser-default custom-select"
                        name="currentSpousePriorSpouseCountryOfCitizenship"
                        value={
                          state.currentSpousePriorSpouseCountryOfCitizenship
                        }
                        onChange={(e) => {
                          onChangeField("k", e.target.name, e.target.value);
                        }}
                        required
                      >
                        <option selected disabled>
                          Choose your option
                        </option>
                        {countries.map((country) => (
                          <option value={country.name}>{country.name}</option>
                        ))}
                      </select>
                    </MDBCol>
                    <MDBCol className="mt-3">
                      <label>Spouse country of Citizenship</label>
                      <select
                        className="browser-default custom-select"
                        name="currentSpousePriorSpouseCountryOfBirth"
                        value={state.currentSpousePriorSpouseCountryOfBirth}
                        onChange={(e) => {
                          onChangeField("k", e.target.name, e.target.value);
                        }}
                        required
                      >
                        <option selected disabled>
                          Choose your option
                        </option>
                        {countries.map((country) => (
                          <option value={country.name}>{country.name}</option>
                        ))}
                      </select>
                    </MDBCol>
                  </MDBRow>
                  <MDBRow className="mt-3">
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Date of Marriage with Prior Spouse"
                        group
                        type="Date"
                        name="dateOfMarriageCurrentSpousePriorSpouse"
                        value={state.dateOfMarriageCurrentSpousePriorSpouse}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0 mt-5"
                      />
                    </MDBCol>
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Date Marriage Ended"
                        group
                        type="Date"
                        name="dateMarriageEndedCurrentSpousePriorSpouse"
                        value={state.dateMarriageEndedCurrentSpousePriorSpouse}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        containerClass="mb-0 mt-5"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow center className="mt-4">
                    <MDBCol md={6} className="align-text-start">
                      <h5 className="text-center">
                        How Current Spouse Marriage Ended
                      </h5>
                    </MDBCol>
                    <MDBCol>
                      <MDBRow>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="marraigeEnded"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "currentSpousePriorSpouseMarriageEndedHow",
                                "Anulled"
                              );
                            }}
                            checked={
                              state.currentSpousePriorSpouseMarriageEndedHow ===
                              "Anulled"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Anulled{" "}
                          </label>
                        </div>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="marraigeEnded"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "currentSpousePriorSpouseMarriageEndedHow",
                                "Divorced"
                              );
                            }}
                            checked={
                              state.currentSpousePriorSpouseMarriageEndedHow ===
                              "Divorced"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Divorced{" "}
                          </label>
                        </div>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="marraigeEnded"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "currentSpousePriorSpouseMarriageEndedHow",
                                "Spouse Deceased"
                              );
                            }}
                            checked={
                              state.currentSpousePriorSpouseMarriageEndedHow ===
                              "Spouse Deceased"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Spouse Deceased{" "}
                          </label>
                        </div>

                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="marraigeEnded"
                            onClick={() => {
                              onChangeField(
                                "e",
                                "currentSpousePriorSpouseMarriageEndedHow",
                                "other"
                              );
                            }}
                            checked={
                              state.currentSpousePriorSpouseMarriageEndedHow ===
                              "other"
                                ? true
                                : false
                            }
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            other{" "}
                          </label>
                        </div>
                      </MDBRow>
                      <MDBRow>
                        {" "}
                        {state.currentSpousePriorSpouseMarriageEndedHow ==
                          "other" && (
                          <MDBCol>
                            <MDBInput
                              color="pink"
                              label="Explain Other"
                              group
                              type="text"
                              name="currentSpousePriorSpouseMarriageEndedHowOther"
                              value={
                                state.currentSpousePriorSpouseMarriageEndedHowOther
                              }
                              onChange={(e) => {
                                validate();
                                onChangeField(e, e.target.name, e.target.value);
                              }}
                              containerClass="mb-0 mt-0"
                            />
                          </MDBCol>
                        )}
                      </MDBRow>
                    </MDBCol>
                  </MDBRow>
                </>
              )}
            </MDBCol>
          </MDBRow>
          {state.noOfTimesCurrentSpouseMarried && (
            <MDBRow center className="d-flex flex-column align-items-center">
              {touched && errorMsg.length > 0 && (
                <div className="text-danger mb-2">{errorMsg}</div>
              )}
              {touched && !errorMsg && (
                <Link to={nextStep}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                </Link>
              )}
              {(!touched || errorMsg) && (
                <MDBBtn
                  color={"grey"}
                  disabled
                  rounded
                  type="button"
                  className="z-depth-1a"
                >
                  Next
                </MDBBtn>
              )}
            </MDBRow>
          )}
        </form>
      </MDBContainer>
    </Step>
  );
}
