
// add q30, q38, q44;
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(0);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [count, setcount] = useState(0);

  const [otherFactors, setOtherFactors] = useState(state.otherFactors30to46);
  const [otherFactorsmul, setOtherFactorsmul] = useState(
    state.otherFactors30to46_multiple
  );
   const Questionsm = [
     {
       question: "Have you EVER ",
       name: "q30",
       options: [
         {
           opt: "A. Been a habitual drunkard?",
           name: "a",
         },
         {
           opt: "B. Been a prostitute, or procured anyone for prostitution?",
           name: "b",
         },
         {
           opt:
             "C. Sold or smuggled controlled substances, illegal drugs, or narcotics?",
           name: "c",
         },
         {
           opt: "D. Been married to more than one person at the same time?",
           name: "d",
         },
         {
           opt: "E. Married someone in order to obtain an immigration benefit?",
           name: "e",
         },
         {
           opt:
             "F. Helped anyone to enter, or try to enter, the United States illegally?",
           name: "f",
         },
         {
           opt:
             "G. Gambled illegally or received income from illegal gambling?",
           name: "g",
         },
         {
           opt: "H. Failed to support your dependents or to pay alimony?",
           name: "h",
         },
         {
           opt:
             "I.	Made any misrepresentation to obtain any public benefit in the United States?",
           name: "i",
         },
       ],
     },
   ];

  const Questions = [
    {
      question:
        "Have you EVER given any U.S. Government officials any information or documentation that was false, fraudulent, or misleading?",
      name: "q31",
    },
    {
      question:
        "Have you EVER lied to any U.S. Government officials to gain entry or admission into the United States or to gain immigration benefits while in the United States?",
      name: "q32",
    },
    {
      question:
        "Have you EVER been removed, excluded, or deported from the United States?",
      name: "q33",
    },
    {
      question:
        "Have you EVER been ordered removed, excluded, or deported from the United States?",
      name: "q34",
    },
    {
      question:
        "Have you EVER been placed in an alternative sentencing or a rehabilitative program (for example, diversion, deferred prosecution, withheld adjudication, deferred adjudication)?",
      name: "q35",
    },
    {
      question:
        "Are removal, exclusion, rescission, or deportation proceedings (including administratively closed proceedings) currently pending against you?",
      name: "q36",
    },
    {
      question: "Have you EVER served in the U.S. armed forces?",
      name: "q37",
    },
    {
      question:
        "Have you EVER been court-martialed, administratively separated, or disciplined, or have you received an other than honorable discharge, while in the U.S. armed forces?",
      name: "q39",
    },
    {
      question:
        "Have you EVER been discharged from training or service in the U.S. armed forces because you were an alien?",
      name: "q40",
    },
    {
      question:
        "Have you EVER left the United States to avoid being drafted in the U.S. armed forces?",
      name: "q41",
    },
    {
      question:
        "Have you EVER applied for any kind of exemption from military service in the U.S. armed forces?",
      name: "q42",
    },
    {
      question: "Have you EVER deserted from the U.S. armed forces?",
      name: "q43",
    },
    
  ];

  const validate = () => {
    setTouched(true);
    var isValid = false;
    if (formRef.current !== null) {
      isValid = formRef.current.checkValidity();
    }
    setErrorMsg(count < 11 ? "Please fill out proper values" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
    validate();
  }, []);

  const handleChange = (name, value) => {
    const values = otherFactors;
    values[name] = value;
    setOtherFactors(values);
    setcount(count + 1);
    validate();
    onChangeField("e", "otherFactors1to21_A", otherFactors);
  };

    const handleChangemul = (qname, oname, value, qindex) => {
      const values = otherFactors;
      validate();
      setcount(count + 1);
      values[qindex]["opts"][oname] = value;
      setOtherFactors(values);
      onChangeField("e", "otherFactors1to21_B", otherFactors);
    };
  return (
    <Step heading={" Other Factors "}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <h6 className="text-center" md={6}>
        If you answer "Yes" to any of these questions, except Item Numbers 37.
        and 38., include a typed or printed explanation on additional sheets of
        paper and provide any evidence to support your answers.
      </h6>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <>
                {Questionsm.map((q, qindex) => (
                  <MDBCard md={12} className="p-4 mt-3">
                    <MDBCol md={12}>
                      <div>
                        <h6
                          style={{
                            width: "100%",
                            fontWeight: "bold",
                            fontSize: "larger",
                          }}
                        >
                          {q.question}
                        </h6>
                      </div>
                    </MDBCol>

                    {q.options.map((o, index) => (
                      <MDBRow className="align-items-center mt-3">
                        <MDBCol md={8}>
                          <div>
                            <h6 style={{ width: "100%" }}>
                               {o.opt}
                            </h6>
                          </div>
                        </MDBCol>
                        <MDBCol>
                          <MDBFormInline className="justify-content-center">
                            <div class="form-check mr-3">
                              <input
                                type="radio"
                                class="form-check-input"
                                name={`${index}`}
                                onClick={() =>
                                  handleChangemul(q.name, o.name, true, qindex)
                                }
                                checked={
                                  otherFactorsmul[qindex]["opts"][o.name] ===
                                  true
                                    ? true
                                    : false
                                }
                                label="Yes"
                                type="radio"
                                id="radio1"
                                style={{ display: "flex" }}
                                containerClass="mr-5"
                                required
                              />
                              <label
                                class="form-check-label"
                                for="materialUnchecked"
                              >
                                Yes
                              </label>
                            </div>
                            <div class="form-check mr-3">
                              <input
                                type="radio"
                                class="form-check-input"
                                name={`${index}`}
                                onClick={() =>
                                  handleChangemul(q.name, o.name, false, qindex)
                                }
                                checked={
                                  otherFactorsmul[qindex]["opts"][o.name] ===
                                  false
                                    ? true
                                    : false
                                }
                                label="Yes"
                                type="radio"
                                id="radio1"
                                style={{ display: "flex" }}
                                containerClass="mr-5"
                                required
                              />
                              <label
                                class="form-check-label"
                                for="materialUnchecked"
                              >
                                No
                              </label>
                            </div>
                          </MDBFormInline>
                        </MDBCol>
                      </MDBRow>
                    ))}
                  </MDBCard>
                ))}
              </>
              {Questions.map((q, index) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                        {". "}
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, true);
                            setTouched(true);
                          }}
                          checked={otherFactors[q.name] === true ? true : false}
                          label="Yes"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          required
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, false);
                            setTouched(true);
                          }}
                          checked={
                            otherFactors[q.name] === false ? true : false
                          }
                          label="No"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                </MDBCard>
              ))}

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
