import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(0);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [count, setcount] = useState(0);

  const [otherFactors, setOtherFactors] = useState(state.otherFactors45to50);

  const Questions = [
    {
      question:
        "Do you support the Constitution and form of Government of the United States?",
      name: "q45",
    },
    {
      question:
        "Do you understand the full Oath of Allegiance to the United States?",
      name: "q46",
    },
    {
      question:
        "Are you willing to take the full Oath of Allegiance to the United States?",
      name: "q47",
    },
    {
      question:
        "If the law requires it, are you willing to bear arms on behalf of the United States?",
      name: "q48",
    },
    {
      question:
        "If the law requires it, are you willing to perform noncombatant services in the U.S. armed forces?",
      name: "q49",
    },
    {
      question:
        "If the law requires it, are you willing to perform work of national importance under civilian direction?",
      name: "q50",
    },
  ];

  const validate = () => {
    setTouched(true);
    var isValid = false;
    if (formRef.current !== null) {
      isValid = formRef.current.checkValidity();
    }
    setErrorMsg(count < 5 ? "Please fill out proper values" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
    
  }, []);

  const handleChange = (name, value) => {
    const values = otherFactors;
    values[name] = value;
    setOtherFactors(values);
    setcount(count + 1);
    validate();
    onChangeField("e", "otherFactors1to21_A", otherFactors);
  };

  return (
    <Step heading={" Other Factors "}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <h6>
        If you answer ''No'' to any of these questions, include a typed or
        printed explanation on additional sheets of paper and provide any
        evidence to support your answers.
      </h6>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {Questions.map((q, index) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                        {". "}
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, true);
                            setTouched(true);
                          }}
                          checked={otherFactors[q.name] === true ? true : false}
                          label="Yes"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          required
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, false);
                            setTouched(true);
                          }}
                          checked={
                            otherFactors[q.name] === false ? true : false
                          }
                          label="No"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                </MDBCard>
              ))}

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
