// add conditionals --shubs

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(0);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [count, setcount] = useState(0);

  const [otherFactors, setOtherFactors] = useState(state.otherFactors1to21_A);

  const Questions = [
    {
      question:
        "Have you EVER claimed to be a U.S. citizen (in writing or any other way)?",
      name: "q1",
    },
    {
      question:
        "Have you EVER registered to vote in any Federal, state, or local election in the United States?",
      name: "q2",
    },
    {
      question:
        "Have you EVER voted in any Federal, state, or local election in the United States?",
      name: "q3",
    },
    {
      question:
        "Have you EVER been declared legally incompetent or been confined to a mental institution?",
      name: "q5",
    },
    {
      question: "Do you owe any overdue Federal, state, or local taxes?",
      name: "q6",
    },

    {
      question:
        "Have you called yourself a non-U.S. resident on a Federal, state, or local tax return since you became a lawful permanent resident?",
      name: "q8",
    },

    {
      question:
        "Have you EVER advocated (either directly or indirectly) the overthrow of any government by force or violence?",
      name: "q11",
    },
    {
      question:
        "Have you EVER persecuted (either directly or indirectly) any person because of race, religion, national origin, membership in a particular social group, or political opinion?",
      name: "q12",
    },
    {
      question:
        "Did you EVER receive any type of military, paramilitary (a group of people who act like a military group but are not part of the official military), or weapons training?",
      name: "q19",
    },
    {
      question:
        "Did you EVER recruit (ask), enlist (sign up), conscript (require), or use any person under 15 years of age to serve in or help an armed force or group?",
      name: "q20",
    },
    {
      question:
        "Did you EVER use any person under 15 years of age to do anything that helped or supported people in combat?",
      name: "q21",
    },
  ];

  const validate = () => {
    setTouched(true);
    var isValid = false;
    if (formRef.current !== null) {
      isValid = formRef.current.checkValidity();
    }
    setErrorMsg(count < 10 ? "Please fill out proper values" : "");
  };

 

  const handleChange = (name, value) => {
    const values = otherFactors;
    console.log(values[1]);
    values[name] = value;
    setOtherFactors(values);
    setcount(count + 1);
    validate();
    onChangeField("e", "otherFactors", otherFactors);
  };

  return (
    <Step heading={" Other Factors "}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {Questions.map((q, index) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                        {". "}
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check mr-3">
                        <input
                          type="radio"
                          class="form-check-input"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, true);
                            setTouched(true);
                          }}
                          checked={otherFactors[q.name] === true ? true : false}
                          label="Yes"
                          type="radio"
                          id="radio1"
                          style={{ display: "flex" }}
                          containerClass="mr-5"
                          required
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Yes
                        </label>
                      </div>
                      <div class="form-check mr-2">
                        <input
                          type="radio"
                          class="form-check-input mr-1"
                          name={`${index}`}
                          onClick={() => {
                            handleChange(q.name, false);
                            setTouched(true);
                          }}
                          checked={
                            otherFactors[q.name] === false ? true : false
                          }
                          label="No"
                          type="radio"
                          id="radio2"
                          style={{ display: "flex" }}
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                </MDBCard>
              ))}

             

             
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
