import React from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step5(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;

  return (
    <Step
      heading={`Do you have a physical or developmental disability or mental impairment that prevents you from demonstrating your knowledge and understanding of the English language and/or civics requirements for naturalization?`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "disability", "Yes");
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "disability", "No");
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
        {state.disability == "Yes" && (
          <>
            <h5 className="text-center mt-5">
              Please submit a completed Form N-648, Medical Certification for
              Disablity Exceptions, when you file your form N400
            </h5>
            <MDBRow center className="d-flex flex-column align-items-center">
              <Link to={nextStep}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="z-depth-1a"
                >
                  Next
                </MDBBtn>
              </Link>
            </MDBRow>
          </>
        )}
      </MDBContainer>
    </Step>
  );
}
